import React from 'react';

import style from './Avatar.module.scss'
import getAvatarColor from '../../../utils/getAvatarColor';

function Avatar({ user }) {

    if (user.avatar) {
        return <img src={user.avatar} className={style.dialog__item_avatar + ' ' + `${user.online && style.online}`} />
    }
    const { color, colorLight } = getAvatarColor(user._id)
    console.log(`linear-gradient(135deg, ${color} 0 %, ${colorLight} 100 %)`)
    return (
        <div style={{ background: `linear-gradient(135deg, ${color} 0%, ${colorLight} 100%)` }} className={style.dialog__item_avatar + ' ' + `${user.online && style.online}`}>{user.fullName[0]}</div>
    )
}

export default Avatar;
