import tinycolor from 'tinycolor2'


export default hash => {
    const [r, g, b] = hash.substr(5, 8).split('').map(char => char.charCodeAt() > 255 ? 255 : char.charCodeAt())

    return {
        color: tinycolor({ r, g, b }).toHexString(),
        colorLight: tinycolor({ r, g, b }).lighten(30).toHexString()
    }
}